.boletos{
    padding: $separacion 0;
    h3{
        text-align: center;
        color:$white;
        
    }
}
.boletos-contain{
    margin-top: $separacion / 2;
    @include grid(2, $separacion);
}
.plan{
    p{
        color: $white;
        font-size: 3rem;
        text-align: center;
        margin-bottom: 2rem;
    }
}
.pass{
    width: 100%;
    border-spacing: 0;
    thead{
        background-color: $pink;
        th{
            padding: $separacion / 2;
            text-transform: uppercase;
            color: $white;
            font-weight: $light;
            font-size: 2rem;
        }
    }
    tbody{
        background-color: $white;
        td{
            padding: $separacion / 4;
            border-bottom: 1px solid #e1e1e1;
        }
        tr{
            &:last-of-type{
                td{
                    border:none;
                }
            }
        }
    }
    tfoot{
        background-color:$blueberry ;
        td{
            color:$white;
            text-align: center;
            padding: $separacion / 4;
            font-size: 2.7rem;
        }
    }
}