.contain-fest{
    padding: $separacion 0;
    @include tablet{
        @include grid (2, $separacion);
    }
    
}
.fest-info{
    display: flex;
    flex-direction: column;
    justify-content: center;
    h2 {
        font-size: 3rem;
    }
    .fecha{
            color: $pink;
            font-weight: $bold;
    }
    .txt{
        line-height: 1.5;
    }
}