.video{
    position: relative;
    max-height: 70rem;
    overflow: hidden;
    
    .overlay{
        position: absolute;
        width: 100%;
        height: 100%;
//* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#75174c+0,a556c4+52,ffffff+100&0.84+0,0.68+52,0.53+100 */
background: -moz-linear-gradient(-45deg,  rgba(117,23,76,0.84) 0%, rgba(165,86,196,0.68) 52%, rgba(255,255,255,0.53) 100%); /* FF3.6-15 */
background: -webkit-linear-gradient(-45deg,  rgba(117,23,76,0.84) 0%,rgba(165,86,196,0.68) 52%,rgba(255,255,255,0.53) 100%); /* Chrome10-25,Safari5.1-6 */
background: linear-gradient(135deg,  rgba(117,23,76,0.84) 0%,rgba(165,86,196,0.68) 52%,rgba(255,255,255,0.53) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#d675174c', endColorstr='#87ffffff',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}
    video{
        display: block; // todos los videos vienen por default como inline
        width: 100%;
    }
}
.contenido-video{
    display: flex;
    align-items: center;
    height: 100%;
    position: relative;
    z-index: 1;

    h2 {
        display: flex;
        flex-direction: column;
        color: $white;
        align-items: flex-start;
        span{
            background-color: $pink;
            border-radius: 1rem;
            font-size: 2rem;
            padding: 1rem 2rem;
        }
    }
}