.galeria{
    padding: $separacion 0;
    h3{
        text-align: center;
    }
}
.galeria-img{
    padding: 0;
    list-style: none;
    margin-top: $separacion;
    @include grid(2, $separacion);
    @include tablet{
        @include grid(4, $separacion / 2);
    }
    @include desktop{
        @include grid(6, $separacion / 2);
    }
}
.overlay{
    position: fixed;
    background-color: rgba(0,0,0,.6);
    width: 100%;
    height: 100%;
    top: 0;
    display: grid;
    place-items: center;
}
.btn-cerrar{
    background-color: $white;
    width: 5rem;
    height: 5rem;
    border-radius: 50%;
    display: grid;
    place-items: center;
    font-weight: $bolder;
    font-size: 3rem;

    &:hover{
        cursor: pointer;
    }
}
