.lineup{
    background-color: $pink;
    padding: $separacion 0;
    h3, p {
        text-align: center;
        color: $white;
    }
    .dia{
        text-transform: uppercase;
        margin:$separacion 0;
    }
}
.escenarios-contain{
    @include tablet{
        @include grid(2,$separacion);
    }
    .escenario{
        padding-top: $separacion;
    }
    .nombre-escenario {
        color: #000;
        font-size: 2rem;
        font-weight: $light;
        &:after{
            content:'';
            width: 20rem;
            height: 10rem;
            display: block;
            margin: 2rem auto 0 auto;
            background-size: 20rem;
            background-repeat: no-repeat;
        }
    }
}
.edm .nombre-escenario{
    &:after {
        background-image: url(../img/icono_edm.png);
    }
}
.rock .nombre-escenario{
    &:after {
        background-image: url(../img/icono_rock.png);
    }
}
.calendario{
    padding: 0;
    list-style: none;
    li{
        padding: $separacion / 2;
        font-weight: $bold;
        span{
            font-weight: $regular;
        }
    }
}
.bg-green .calendario li:nth-child(odd){
    background-color: darken($green, 10) ;
}
.bg-yellow .calendario li:nth-child(odd){
    background-color: darken($yellow, 10) ;
}